import onAction from './handlers/onAction';
import * as actions from './actions';
import { BiPluginConfig, BiPlugin } from '../../types';
import createOnKeyDown from './handlers/createOnKeyDown';
import createOnChange from './handlers/createOnChange';
import createOnConstruct from './handlers/createOnConstruct';
import createOnKeyUp from './handlers/createOnKeyUp';
import createRenderEditable from './createRenderEditable';
import renderLeftToolbar from './components/insertBtn';
import { queries } from './queries';

function createNewlineGuidePlugin(config: BiPluginConfig = {}): BiPlugin {
  return {
    renderEditable: createRenderEditable(config),
    onAction,
    onKeyUp: createOnKeyUp(config),
    onKeyDown: createOnKeyDown(config),
    models: [],
    queries,
    leftToolbar: renderLeftToolbar(config),
    onChange: createOnChange(config),
    onConstruct: createOnConstruct(config),
  };
}

createNewlineGuidePlugin.actions = actions;

export default createNewlineGuidePlugin;
